import React from 'react'
import Link from 'gatsby-link'

const ProvidersAndStaff = () => (
  <div className="container">
    <h1>Providers And Staff</h1>
    <br />
    <Link to="/">Go back to the homepage</Link>
  </div>
)

export default ProvidersAndStaff



